*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
}
.container{
    height: calc(100vh - 120px);
    width: 100%;
    overflow-y: scroll;
    background: white;
    overflow-x:hidden;
}
.End{
    height: 100px;
    width: 95%;
    font-size: 23px;
    font-weight: 400;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    margin-left: 2.5%;
}